@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

.btn-blue {
  @apply inline-block bg-blue-500 text-white px-4 py-2 rounded;
}

.btn-red {
  @apply inline-block bg-red-500 text-white px-4 py-2 rounded;
}

.btn-outline-blue {
  @apply inline-block border border-blue-500 text-blue-900 px-4 py-2 rounded;
}

.btn-outline-red {
  @apply inline-block border border-red-500 text-red-900 px-4 py-2 rounded;
}
